//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

$mainFamily: 'Montserrat';
$gothamPro: 'GothamPro';
$OpenSans: 'OpenSans';
$lora: 'Lora';
$roboto: 'Roboto';

@mixin fmt {
	font-family: $mainFamily, Arial, sans-serif; }
@mixin fgp {
	font-family: $gothamPro, Arial, sans-serif; }
@mixin fos {
	font-family: $OpenSans, Arial, sans-serif; }
@mixin flora {
	font-family: $lora, serif, sans-serif; }
@mixin frob {
	font-family: $roboto, Arial, sans-serif; }

@font-face {
	@include fontdefault(normal, 900, $mainFamily);
	@include fontpath('Montserrat Black', 'Montserrat-Black', Montserrat-Black); }
@font-face {
	@include fontdefault(italic, 900, $mainFamily);
	@include fontpath('Montserrat BlackItalic', 'Montserrat-BlackItalic', Montserrat-BlackItalic); }

