.btn {
	padding: 16px 20px;
	min-width: 240px;
	color: $red;
	@include fmt;
	@include f(16px,600,1.375);
	text-align: center;
	vertical-align: top;
	border-radius: 4px;
	border: 1px solid #CFCFD9;
	background-color: transparent;
	transition: border-color .3s;
	display: inline-block;
	@include hover {
		border-color: $red; }
	&:disabled {
		color: #CFCFD9;
		cursor: default;
		@include hover {
			border-color: #CFCFD9; } }
	&_full {
		width: 100%; } }
.btn-simple {
	padding: 14px 20px;
	min-width: 160px;
	color: $white;
	@include fgp;
	@include f(14px,500,1.43);
	text-align: center;
	border-radius: 4px;
	background-color: $red;
	transition: background-color .3s;
	vertical-align: top;
	display: inline-block;
	@include hover {
		background-color: #FB695E;
		&:active {
			background-color: #EF4A3E; } }
	&:active {
		background-color: #EF4A3E; }
	&:disabled {
		color: #A9A9B2;
		cursor: default;
		background-color: #CFCFD9;
		@include hover {
			background-color: #CFCFD9; } }
	@include md {
		min-width: 114px; }
	&_full {
		width: 100%; }
	&_sm {
		padding: 6px 17px;
		font-size: 12px;
		min-width: 0;
		font-weight: 600;
		line-height: 1.5;
		border-radius: 3px; }
	&_white {
		color: #000;
		background-color: $white;
		@include hover {
			background-color: darken($white, 15%); } }
	&_os {
		@include fos; } }
.btn-sm {
	padding: 8px 12px;
	min-width: 146px;
	color: #1D50DE;
	@include fos;
	@include f(14px,600,1.43);
	text-align: center;
	border-radius: 3px;
	transition: background-color .3s, color .3s;
	vertical-align: top;
	display: inline-block;
	border: 1px solid #1D50DE;
	background-color: transparent;
	@include hover {
		color: $white;
		background-color: #1D50DE; }
	.red-theme & {
		border-color: #C82525;
		color: #C82525; } }
.red-theme.no-touch {
	.btn-sm {
		&:hover {
			background-color: #C82525; } } }
