.soc {
	align-items: center;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	&__description {
		padding-right: 10px;
		@include f(14px,400,1.43);
		color: $gray;
		@include md {
			padding-right: 25px; }
		@include sm {
			padding-right: 15px;
			font-size: 13px; } }
	&__list {
		align-items: center;
		flex-wrap: wrap;
		display: flex; }
	&__item {
		padding: 2px 0;
		font-size: 0;
		line-height: 1;
		&:not(:last-child) {
			margin-right: 28px;
			@include md {
				margin-right: 25px; }
			@include sm {
				margin-right: 22px; } }
		a {
			color: currentColor;
			transition: color .3s;
			display: block;
			@include hover {
				color: $red;
				&:active {
					color: #EF4A3E; } }
			&:active {
				color: #EF4A3E; } }
		.icon {
			font-size: 20px; } } }

.share,
.assessment {
	@include frob;
	@include f(13px,400,1.2);
	color: #B6B6B6;
	&__text {
		margin-bottom: 18px;
		@include sm {
			margin-bottom: 8px; } }
	&__list {
		font-size: 0;
		@include sm {
			flex-wrap: wrap;
			display: flex; }
		li {
			display: block;
			&:not(:last-child) {
				margin-bottom: 8px;
				@include sm {
					margin: 0 10px 8px 0; } } }
		a {
			width: 44px;
			height: 44px;
			color: #999;
			font-size: 19px;
			line-height: 47px;
			text-align: center;
			background-color: #F4F4F4;
			border-radius: 4px;
			transition: color .3s, background-color .3s;
			display: inline-block;
			@include sm {
				width: 41px;
				height: 41px;
				line-height: 45px; }
			@include hover {
				background-color: #3067FF;
				color: $white; }
			.red-theme.no-touch & {
				&:hover {
					background-color: #C82525; } } } } }
.assessment {
	&__numb {
		color: #1D50DE;
		.red-theme & {
			color: $red; } }
	&__list {
		a {
			color: #979797; } } }

.soc-mobile {
	margin: 20px 0;
	flex-wrap: wrap;
	display: flex;
	display: none;
	@include sm {
		display: flex; }
	.share,
	.assessment {
		&:not(:last-child) {
			@include sm {
				margin: 0 10px 0 0; } } } }
