	ul:not([class]),
	ol:not([class]) {
		&:not(:first-child) {
			margin-top: 30px;
			@include sm {
				margin-top: 15px; } }
		&:not(:last-child) {
			margin-bottom: 30px;
			@include sm {
				margin-bottom: 15px; } }
		li {
			&:not(:first-child) {
