	ul {
		&:not([class]) {
			li {
				position: relative;
				&:before {
					content: '';
					width: 6px;
					height: 6px;
					margin: 10px 10px 0 0;
					vertical-align: top;
					border-radius: 50%;
					background-color: #333;
					display: inline-block; } } } }
	ol {
		&:not([class]) {
			counter-reset: list;
			li {
				&:before {
					counter-increment: list;
					content: counter(list) ") "; } } } }
